.main-container {
  width: 100%;
  margin: 20px 0 20px 0;
}

.select-wrape {
  display: flex;
  /* width: 200px; */
  /* height: 30px; */
}

.select-wrape .Select {
  max-width: 200px;
}

.graphs-container {
  height: calc(100vh - 56px);
  padding: 20px;
  overflow: hidden;
}

.graph-container {
  border: 2px solid;
}

.graphs-container > .row {
  height: 50%;
  padding-bottom: 20px;
}


.graphs-container .row .graph-container > div {
  height: 100%;
}
.graphs-container .row .graph-container > .select-wrape {
  height: 20px;
}

.graphs-container .row .graph-container > div > div {
  height: calc(100% - 40px);
}

.graphs-container .row .graph-container > div > div .react-stockchart {
  height: 100%!important;
}

.graphs-container .row .graph-container > div > div .react-stockchart > div {
  height: 100%;
}
.graphs-container .row .graph-container > div > div .react-stockchart > div canvas {
  height: 100%!important;
}

.graphs-container .row .graph-container > div label {
  display: none;
}

.graphs-container .row .graph-container > div select {
  display: none;
}
.select-wrape{
  align-items: center;
  margin-top: 10px;
}
.select-wrape > div {
  display: table;
  margin-left: 10px;
}

.select-wrape .Select {
  z-index: 1000;
  position: relative;
  width: 100%;
}

.select-option {
  width: 200px;
  z-index: 1000;
  position: relative;
}

#type {
  display: none;
}

.select-wrape ~ div label {
  display: none;
}